.movie_card {
    position:relative;
    border-radius: 6px;
    background-color: #fff;
    box-shadow: 0 4px 7px rgba(#000, 0.1);
    overflow: hidden;
}

.movie_link {
    display: block;
}

.movie_img_none,
.movie_img_wrap {
    &::before {
        content: "";
        display: inline-block;
        padding-top: 150%;
        vertical-align: top;
    }
}

.movie_img_wrap {
    overflow: hidden;

    &:hover {
        > img {
            transform: scale(1.05);
            transform-origin: center;
            transition: transform 0.3s ease-in-out;
        }
    }
}

.movie_img_none {
    display: flex;
    align-items: center;
    background-color: #f4f4f4;
    justify-content: center;

    > span {
        font: {
            size: 18px;
            weight: bold;
        }
    }
}

.movie_info {
    padding: 10px 16px;

    .movie_title {
        font:{
            size: 20px;
            weight: bold;
        }
        line-height: 26px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .movie_summary {
        margin-top: 10px;
        font-size: 16px;
        line-height: 18px;
        min-height: 54px;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        display: -webkit-box;
    }

    .movie_genres {
        margin-top: 10px;
        overflow: hidden;

        .movie_genres_item {
            color: #777;
            font-size: 14px;
            float: left;

            & + .movie_genres_item::before {
                content: "";
                margin: 5px 6px 0 6px;
                display: inline-block;
                width: 3px;
                height: 3px;
                background-color: #777;
                border-radius: 50%;
                vertical-align: top;
            }
        }
    }
}

.movie_btn_detail {
    padding: 10px 0;
    font: {
        size: 18px;
    }
    line-height: 22px;
    text-align: center;
}