.home_movie_wrap {
    max-width: 1024px;
    margin: 0 auto;
    padding: 150px;
    background-color: #fff;
}

.home_movie_list {
    overflow: hidden;
}

.home_movie_item {
    width: 33.33%;
    padding: 0 10px 40px;
    box-sizing: border-box;
    float: left;

    &:nth-child(3n+1) {
        clear: both;
    }
}